import Dice from './dice.js';

const dice = Dice();
const tinycolor = require("tinycolor2");

function insertIntoNewArray(arr, i, item) {
  return arr.map(function(orig, j) {return (i === j) ? item : orig; });
}

function arrayToRange(arr) {
  return arr.map(function(_, i) { return i; });
}

function escapeRegExp(str) {
  return str.replace(/[-[\]/{}()*+?.\\^$|]/g, "\\$&");
}

export {dice, tinycolor, insertIntoNewArray, arrayToRange, escapeRegExp};


