import React, { Component } from 'react';
import * as utils from './utils.js';
import './mobu.css';

const MODE = {
  SEL_CG: "Select creature group",
  SEL_C: "Select individual creature",
  SEL_A: "Select action for ",
  SEL_CD: "Select condition for ",
  GET_D: "Enter damage",
  GET_H: "Enter amount healed",
  RESULT: "Result for "
};

var defActions = [{name: "Damage", icon: "sword"}, {name: "Heal", icon: "heal"}];

function padActions(actions) {
  return defActions.concat(actions);
}

class Runner extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mode: MODE.SEL_CG,
      isSelecting: true,
      isMultiSelect: false,
      isValidExp: false,
      isWaitingDblClick: false,
      input: "",
      active: props.creatures.slice(),
      displayed: utils.arrayToRange(props.creatures),
      meta: {cg: -1, c: [], a: -1, cd: -1},
      history: [],
      historyCount: 0
    };

    defActions = defActions.slice(0, 2);
    if(this.props.conditions.length > 0){
      defActions.push({name: "Condition", icon: "haze"});
    }
  }

  resetState() {
    this.setState({
      mode: MODE.SEL_CG,
      isSelecting: true,
      isMultiSelect: false,
      isValidExp: false,
      isWaitingDblClick: false,
      input: "",
      active: this.props.creatures.slice(),
      displayed: utils.arrayToRange(this.props.creatures),
      meta: {cg: -1, c: [], a: -1, cd: -1}
    });
  }

  toHistoryStep(i) {
    const step = this.state.history[i];
    this.setState({
      input: "",
      isSelecting: true,
      mode: MODE.RESULT,
      active: step.result,
      displayed: utils.arrayToRange(step.result),
      meta: step.meta
    });
  }

  toActionMode(meta) {
    const cg = this.props.creatures[meta.cg];
    const paddedActions = padActions(cg.actions);
    this.setState({
      input: "",
      mode: MODE.SEL_A,
      active: paddedActions,
      displayed: utils.arrayToRange(paddedActions),
      meta: meta
    });
  }

  getResultMessage() {
    // This shouldn't happen, but just in case.
    if(this.state.meta.cg < 0 || this.state.meta.a < 0) return null;

    const cg = this.props.creatures[this.state.meta.cg];
    switch(this.state.meta.a) {
      case 0: // Damage
        return "taking damage"
      case 1: // Heal
        return "healing"
      case 2: // Condition
        if(this.props.conditions.length > 0)
          return "toggling condition " + this.props.conditions[this.state.meta.cd].name;
        // Fallthrough if no conditions
      default:
        return cg.actions[this.state.meta.a - defActions.length].name
    }
  }

  handleInputChange(e) {
    const query = e.target.value;

    // Keep state up to date.
    this.setState({
      input: query
    });

    if(this.state.isSelecting) {

      // Spacebar is always cancel. Also reset if in result mode.
      if(query.endsWith(" ") || (query && this.state.mode === MODE.RESULT)) return this.resetState();

      // Narrow down the displayed results based on match of query string.
      const pattern = new RegExp("^" + utils.escapeRegExp(query) + ".*$", "i");
      const newDisplayed = [];
      this.state.active.forEach((item, i) => {
        if(item.name.match(pattern)) newDisplayed.push(i);
      });

      // When we're down to one, select it or add to multi-selection.
      if(newDisplayed.length === 1){
        this.select(newDisplayed[0]);
      }
      else if (newDisplayed.length === 0 && this.state.isMultiSelect) {
        this.setState({ input: "" });
      }
      else {
        this.setState({ displayed: newDisplayed });
      }
    }
    else {
      // We're looking for a dice expression.
      this.setState({
        isValidExp: (utils.dice.parse(query)) ? true : false
      });
    }
  }

  handleMetaKeys(e) {
    // Shift key
    if(e.which === 16 && this.state.mode === MODE.SEL_C) {
      this.setState({
        isMultiSelect: (e.type === "keydown")
      });
      if(e.type === "keyup" && this.state.meta.c.length > 0) {
        this.toActionMode(this.state.meta);
      }
    }
    // Enter key
    else if(e.which === 13 && e.type === "keydown") {
      if(this.state.mode === MODE.SEL_C && this.state.isMultiSelect) this.selectAll();
      else if(this.state.mode === MODE.RESULT) this.resetState();
    }
  }

  handleSelectToggle(e) {
    if(this.state.isWaitingDblClick) return this.selectAll();
    const checked = e.target.checked;
    this.setState({
      isMultiSelect: checked,
      isWaitingDblClick: true
    });
    setTimeout(() => this.setState({isWaitingDblClick: false}), 300);
    if(!checked && this.state.meta.c.length > 0) {
      this.toActionMode(this.state.meta);
    }
  }

  handleNumpadChange(key) {
    let exp;
    if(key === "DEL") exp = (this.state.input.length > 0) ? this.state.input.slice(0, -1) : "";
    else exp = this.state.input + key;
    this.setState({
      input: exp,
      isValidExp: (utils.dice.parse(exp)) ? true : false
    });
  }

  addToHistory(meta, result) {
    return this.state.history.slice(-7).concat([{meta: meta, result: result}]);
  }

  doneInput(e) {
    e.preventDefault();
    if(!this.state.isSelecting) {
      const exp = utils.dice.parse(this.state.input);
      const cg = this.props.creatures[this.state.meta.cg];
      const newMembers = cg.members.slice();
      const usePrefix = (newMembers.length > 1);
      const result = this.state.meta.c.map((i) => {
        const c = newMembers[i];
        const delta = exp.roll();
        // Damage
        if(this.state.mode === MODE.GET_D) {
          c.HP = Math.max(c.HP - delta, 0);
          return {
            name: " ",
            title: (usePrefix) ? `${c.name}: ${c.desc}` : c.desc,
            desc: [<p key="dmg">took <strong>{delta}</strong> damage.</p>,
              (c.HP === 0) ? <p key="dead" className="mobu-hp-dead">Dead</p> : null],
            HP: c.HP,
            maxHP: c.maxHP,
            color: c.color
          };
        }
        // Heal
        else {
          c.HP = Math.min(c.HP + delta, c.maxHP);
          return {
            name: " ",
            title: (usePrefix) ? `${c.name}: ${c.desc}` : c.desc,
            desc: <p>healed <strong>{delta}</strong>.</p>,
            HP: c.HP,
            maxHP: c.maxHP,
            color: c.color
          };
        }
      });

      const newCg = {...cg, members: newMembers};
      this.props.onUpdate(this.state.meta.cg, newCg);
      this.setState({
        input: "",
        isSelecting: true,
        mode: MODE.RESULT,
        active: result,
        displayed: utils.arrayToRange(result),
        history: this.addToHistory(this.state.meta, result),
        historyCount: this.state.historyCount + 1
      });
    }
  }

  selectCG(i) {
    const cg = this.state.active[i];
    const newMeta = {...this.state.meta, cg: i};
    if(cg.number > 1) {
      this.setState({
        input: "",
        mode: MODE.SEL_C,
        active: cg.members.slice(),
        displayed: utils.arrayToRange(cg.members),
        meta: newMeta
      });
    }
    else {
      this.toActionMode({...newMeta, c: [0]});
    }
  }

  selectCreature(i) {
    if(this.state.isMultiSelect) {
      let idx = -1;
      let newSelected;

      // If it's already selected, toggle.
      this.state.meta.c.forEach((j, jdx) => { if(j === i) idx = jdx; });
      if(idx > -1) {
        newSelected = this.state.meta.c.slice();
        newSelected.splice(idx, 1);
      }
      // Otherwise, add to selection.
      else {
        newSelected = this.state.meta.c.concat([i]).sort(function(a, b) { return a - b; });
      }
      this.setState({
        input: "",
        meta: {...this.state.meta, c: newSelected}
      });  
    }
    // If any item has been selected at all and it's not multi-select,
    // then this must be the first and only item selected. Go to action mode.
    else {
      this.toActionMode({...this.state.meta, c: [i]});
    }
  }

  selectAction(i) {
    const action = this.state.active[i];
    const newMeta = {...this.state.meta, a: i};
    if(action.name === "Damage") {
      this.setState({
        input: "",
        mode: MODE.GET_D,
        isSelecting: false,
        displayed: [],
        meta: newMeta
      });
    }
    else if(action.name === "Heal") {
      this.setState({
        input: "",
        mode: MODE.GET_H,
        isSelecting: false,
        displayed: [],
        meta: newMeta
      });
    }
    else if(action.name === "Condition") {
      this.setState({
        input: "",
        mode: MODE.SEL_CD,
        active: this.props.conditions,
        displayed: utils.arrayToRange(this.props.conditions),
        meta: newMeta
      });
    }
    else {
      const cg = this.props.creatures[this.state.meta.cg];
      let result;
      if(cg.number === 1) {
        const c = cg.members[this.state.meta.c[0]];
        result = [{name: " ", title: c.desc, desc: evalDesc(action.desc), color: c.color}];
      }
      else {
        result = this.state.meta.c.map((i) => {
          const c = cg.members[i];
          return {name: " ", title: `${c.name}: ${c.desc}`, desc: evalDesc(action.desc), color: c.color};
        });
      } 
      this.setState({
        input: "",
        isSelecting: true,
        mode: MODE.RESULT,
        active: result,
        displayed: utils.arrayToRange(result),
        meta: newMeta,
        history: this.addToHistory(newMeta, result),
        historyCount: this.state.historyCount + 1
      });
    }
  }

  selectCondition(i) {
    const newMeta = {...this.state.meta, cd: i};
    const cg = this.props.creatures[this.state.meta.cg];
    const cd = this.props.conditions[i];
    const newMembers = cg.members.slice();
    const result = this.state.meta.c.map((ci) => {
      const c = newMembers[ci];
      const newConditions = c.conditions.slice();
      let desc;
      // Remove status condition if contains
      if(newConditions.includes(i)) {
        newConditions.splice(newConditions.indexOf(i), 1);
        desc = "Removed " + cd.name;
      }
      // Add status condition if not
      else {
        newConditions.push(i)
        newConditions.sort(function(a, b) { return a - b; });
        desc = "Added " + cd.name;
      }
      c.conditions = newConditions;
      return {
        name: " ",
        title: `${c.name}: ${c.desc}`,
        desc: <p>{desc}</p>,
        color: c.color,
        condition: i
      };
    });
    this.props.onUpdate(this.state.meta.cg, {...cg, members: newMembers});
    this.setState({
      input: "",
      isSelecting: true,
      mode: MODE.RESULT,
      active: result,
      displayed: utils.arrayToRange(result),
      meta: newMeta,
      history: this.addToHistory(newMeta, result),
      historyCount: this.state.historyCount + 1
    });
  }

  select(item) {
    if(this.state.mode === MODE.SEL_CG) this.selectCG(item);
    else if(this.state.mode === MODE.SEL_C) this.selectCreature(item);
    else if(this.state.mode === MODE.SEL_A) this.selectAction(item);
    else if(this.state.mode === MODE.SEL_CD) this.selectCondition(item);
  }

  selectAll() {
    if(this.state.mode === MODE.SEL_C)
      this.toActionMode({...this.state.meta, c: utils.arrayToRange(this.state.active)});
  }

  render() {
    const listContent = this.state.displayed.map((i) => {
      const item = this.state.active[i];
      switch(this.state.mode) {
        case MODE.SEL_CG:
          return <CreatureGroupCard key={i} cg={item}
                  onClick={() => this.selectCG(i)}
                  conditions={this.props.conditions}
                />;
        case MODE.SEL_C:
          return <CreatureCard key={i} c={item}
                  onClick={() => this.selectCreature(i)}
                  conditions={this.props.conditions}
                  selected={this.state.meta.c.some((j) => { return (j === i); })}
                />;
        case MODE.SEL_A:
          const cg = this.props.creatures[this.state.meta.cg];
          return <ActionCard key={i} a={item} color={cg.color} onClick={() => this.selectAction(i)}/>;
        case MODE.SEL_CD:
          return <ConditionCard key={i} c={item} onClick={() => this.selectCondition(i)}/>
        case MODE.RESULT:
          return <ResultCard key={i} r={item} onClick={() => this.resetState()}/>
        default:
          return null;
      }
    });

    let listClass = "mobu-run-list";
    if(this.state.active.length > 5) listClass += " five-up";
    else if(this.state.active.length > 2) listClass += " four-up";

    const histListNames = this.state.history.map((h) => {
      const cg = this.props.creatures[h.meta.cg];
      return cg.name + " " + padActions(cg.actions)[h.meta.a].name;
    });

    return (
      <div className="mobu-component-container">
        <h1 className="mobu-run-instruction">
          {this.state.mode}
          {(this.state.mode === MODE.SEL_A || this.state.mode === MODE.SEL_CD) &&
            this.props.creatures[this.state.meta.cg].name
             + ((this.state.meta.c.length > 1) ? " group" : "")}
          {(this.state.mode === MODE.RESULT) &&
            this.getResultMessage()}
        </h1>
        <form className="row mobu-run-input" onSubmit={(e) => this.doneInput(e)}>
          <input
            type="text"
            className={`form-control mobu-run-input-text${(this.state.isSelecting) ? "" : " col-9"}`}
            onChange={(e) => this.handleInputChange(e)}
            onKeyDown={(e) => this.handleMetaKeys(e)}
            onKeyUp={(e) => this.handleMetaKeys(e)}
            value={this.state.input}
            disabled={this.state.active.length === 0 && this.state.isSelecting}
            placeholder={(this.state.isSelecting) ? ((this.state.mode === MODE.RESULT) ? "Press any key" : "Begin typing option name") : ""}
          />
          <div className={(this.state.isSelecting) ? "" : "col-3"}>
            <input
              type="submit"
              value="Done"
              className="btn btn-secondary mobu-run-input-btn"
              disabled={!this.state.isValidExp || this.state.isSelecting}
              hidden={this.state.isSelecting}
            />
          </div>
        </form>
        <div className={listClass} onClick={(this.state.mode === MODE.RESULT) ? () => this.resetState() : null}>
          {(this.state.mode !== MODE.SEL_CG && this.state.mode !== MODE.RESULT && this.state.isSelecting) &&
            <CancelCard onClick={() => this.resetState()}/>
          }
          {(this.state.mode === MODE.SEL_C) &&
            <SelectToggle
              checked={this.state.isMultiSelect}
              onChange={(e) => this.handleSelectToggle(e)}
              numSelected={this.state.meta.c.length}
            />
          }
          {(!this.state.isSelecting) &&
            <DiceNumPad onClick={(key) => this.handleNumpadChange(key)}/>
          }
          {listContent}
        </div>
        <History
          list={histListNames}
          onClick={(i) => this.toHistoryStep(i)}
          offset={Math.max(this.state.historyCount - 8, 0)}
        />
      </div>
    );
  }
}

function CreatureGroupCard(props) {
  const actionList = props.cg.actions.map((a, i) => {
    return (
      <li key={i} className="list-inline-item badge badge-pill badge-secondary">{a.name}</li>
    );
  })
  return (
    <div className="mobu-run-block" style={{backgroundColor: props.cg.color}} onClick={props.onClick}>
      <div className="mobu-run-block-content">
        <h3 className="mobu-card-title">{props.cg.name}{(props.cg.number > 1) ? ` (${props.cg.number})` : ""}</h3>
        {(props.cg.number === 1)
          ? <ConditionIcons conditions={props.conditions} c={props.cg.members[0]}/>
          : <ConditionIcons conditions={props.conditions} cg={props.cg}/>}
        <ul className="list-inline">
          {actionList}
        </ul>
      </div>
      {(props.cg.number === 1) &&
        <HPBar HP={props.cg.members[0].HP} maxHP={props.cg.members[0].maxHP}/>}
    </div>
  );
}

function CreatureCard(props) {
  const color = (props.selected) ? props.c.color.clone().darken() : props.c.color;
  let className = "mobu-run-block";
  if(props.selected) className += " selected";
  return (
    <div className={className} style={{backgroundColor: color}} onClick={props.onClick}>
      <div className="mobu-run-block-content">
        <h4 className="mobu-card-title">{props.c.name}: {props.c.desc}</h4>
        <ConditionIcons conditions={props.conditions} c={props.c}/>
        {(props.c.HP === 0) &&
          <p className="mobu-hp-dead">Dead</p>}
      </div>
      <HPBar HP={props.c.HP} maxHP={props.c.maxHP}/>
    </div>
  );
}

function ActionCard(props) {
  let cardClass = "mobu-run-block";
  if(props.a.icon) cardClass += " action-icon " + props.a.icon;
  return (
    <div className={cardClass} onClick={props.onClick} style={{backgroundColor: props.color}}>
      <div className="mobu-run-block-content">
        <h2 className="mobu-card-title">{props.a.name}</h2>
      </div>
    </div>
  );
}

function ConditionCard(props) {
  return (
    <div className="mobu-run-block" onClick={props.onClick} style={{backgroundColor: props.c.color}}>
      <div className="mobu-run-block-content">
        <h3 className="mobu-card-title light">{props.c.name.replace("-", "")}</h3>
      </div>
    </div>
  );
}

function CancelCard(props) {
  return (
    <div className="mobu-run-block whiteout" onClick={props.onClick}>
      <div className="mobu-run-block-content">
        <h3 className="mobu-card-title">Cancel</h3>
        <p>[Space bar]</p>
      </div>
    </div>
  );
}

function SelectToggle(props) {
  const hasSelection = (props.checked && props.numSelected > 0);
  return (
    <div className="mobu-run-block whiteout">
      <input type="checkbox" id="selectToggle" checked={props.checked} onChange={props.onChange}/>
      <label className="full-checkbox" htmlFor="selectToggle">
        <div className="mobu-run-block-content">
            <h4 className="mobu-card-title">{(hasSelection) ? "Done selecting" : "Select multiple"}</h4>
            <p>{(hasSelection) ? "[Release shift]" : "[Hold shift]"}</p>
            <p>Double-click or [shift-enter] selects all</p>
        </div>
      </label>
    </div>
  );
}

function ResultCard(props) {
  return (
    <div className="mobu-run-block" onClick={props.onClick} style={{backgroundColor: props.r.color}}>
      <div className="mobu-run-block-content">
        <h3 className="mobu-card-title">{props.r.title}</h3>
        {props.r.desc}
      </div>
      {(props.r.maxHP) &&
        <HPBar HP={props.r.HP} maxHP={props.r.maxHP}/>}
    </div>
  );
}

function HPBar(props) {
  const width = (props.HP / props.maxHP) * 100;
  const barStyle = (width < 25) ? "mobu-hp-bar critical" : "mobu-hp-bar";
  return (
    <div className="mobu-hp-container">
      <div className={barStyle} style={{width: width + "%"}}></div>
      <span className="mobu-hp-text">{props.HP}/{props.maxHP}</span>
    </div>
  );
}

function ConditionIcons(props) {
  // If it's a creature group
  let iconList;
  if(props.cg) {
    // Get a frequency count
    const freqs = props.conditions.map(function() { return 0; });
    props.cg.members.forEach(function(c) {
      c.conditions.forEach(function(i) { freqs[i]++; });
    });

    const deathCount = props.cg.members.reduce(function(a, b) { return a + (b.HP === 0 ? 1 : 0); }, 0);

    iconList = freqs.map(function(f, i) {
      const cd = props.conditions[i];
      if(f > 0) return (
        <span key={i} className="badge badge-pill condition" title={cd.name} style={{backgroundColor: cd.color}}>
          {f}
        </span>
      );
      else return null;
    });

    if(deathCount > 0) iconList.unshift(
      <span key="death" className="badge badge-pill death condition" title="Dead">{deathCount}</span>
    );
  }
  else {
    iconList = props.c.conditions.map(function(i, j) {
      const cd = props.conditions[i];
      return (
        <span key={j} className="badge badge-pill condition" title={cd.name} style={{backgroundColor: cd.color}}>
          &nbsp;
        </span>
      );
    });

    if(props.c.HP === 0) iconList.unshift(
      <span key="death" className="badge badge-pill death condition" title="Dead">&nbsp;</span>
    );
  }
  return (
    <div className="mobu-conditions-row">
      {iconList}
    </div>
  );
}

function DiceNumPad(props) {
  return (
    <div className="mobu-numpad">
      <div className="mobu-numpad-row">
        <button className="btn btn-secondary mobu-numpad-key" type="button" onClick={() => props.onClick("7")}>7</button>
        <button className="btn btn-secondary mobu-numpad-key" type="button" onClick={() => props.onClick("8")}>8</button>
        <button className="btn btn-secondary mobu-numpad-key" type="button" onClick={() => props.onClick("9")}>9</button>
        <button className="btn btn-secondary mobu-numpad-key" type="button" onClick={() => props.onClick("+")}>+</button>
        <button className="btn btn-secondary mobu-numpad-key" type="button" onClick={() => props.onClick("DEL")}><i className="fa fa-arrow-left" aria-hidden="true"></i></button>
      </div>
      <div className="mobu-numpad-row">
        <button className="btn btn-secondary mobu-numpad-key" type="button" onClick={() => props.onClick("4")}>4</button>
        <button className="btn btn-secondary mobu-numpad-key" type="button" onClick={() => props.onClick("5")}>5</button>
        <button className="btn btn-secondary mobu-numpad-key" type="button" onClick={() => props.onClick("6")}>6</button>
        <button className="btn btn-secondary mobu-numpad-key" type="button" onClick={() => props.onClick("-")}>&minus;</button>
        <button className="btn btn-secondary mobu-numpad-key" type="button" onClick={() => props.onClick("(")}>(</button>
      </div>
      <div className="mobu-numpad-row">
        <button className="btn btn-secondary mobu-numpad-key" type="button" onClick={() => props.onClick("1")}>1</button>
        <button className="btn btn-secondary mobu-numpad-key" type="button" onClick={() => props.onClick("2")}>2</button>
        <button className="btn btn-secondary mobu-numpad-key" type="button" onClick={() => props.onClick("3")}>3</button>
        <button className="btn btn-secondary mobu-numpad-key" type="button" onClick={() => props.onClick("*")}>&times;</button>
        <button className="btn btn-secondary mobu-numpad-key" type="button" onClick={() => props.onClick(")")}>)</button>
      </div>
      <div className="mobu-numpad-row">
        <button className="btn btn-secondary mobu-numpad-tplkey" type="button" onClick={() => props.onClick("0")}>0</button>
        <button className="btn btn-secondary mobu-numpad-key" type="button" onClick={() => props.onClick("/")}>&divide;</button>
        <button className="btn btn-secondary mobu-numpad-key" type="button" onClick={() => props.onClick("d")}>d</button>
      </div>
    </div>
  );
}

function History(props) {
  const histList = props.list.map((h, i) => {
    return (
      <li key={i} className="list-group-item">
        <button type="button" className="btn btn-link" onClick={() => props.onClick(i)}>{i + 1 + props.offset}. {h}</button>
      </li>
    );
  });
  return (
    <div className="card mobu-history">
      <div className="card-header">
        <h4><a data-toggle="collapse" href="#mobuHistory" role="button" aria-expanded="true" aria-controls="mobuHistory">History</a></h4>
      </div>
      <div id="mobuHistory" className="collapse">
        <ul className="list-group list-group-flush">
          {histList}
        </ul>
      </div>
    </div>
  );
}

function evalDesc(desc) {
  // A little dirty, but we want to guarantee that the string
  // does not begin or end with a dice expression.
  desc = " " + desc + " ";
  let exps = [];
  desc = desc.replace(/\[([^[\]]+)\]/g, (orig, exp, offset) => {
    let dexp = utils.dice.parse(exp);
    if(!dexp) return orig;

    let res = dexp.roll();
    if(res === dexp.max() && !dexp.static)
      exps.push(<strong key={offset} className="critical">{res}</strong>);
    else if(res === dexp.min() && !dexp.static)
      exps.push(<strong key={offset} className="fail">{res}</strong>);
    else
      exps.push(<strong key={offset}>{res}</strong>);
    return "%EXP%";
  });
  // Interleave literals with expressions.
  let lits = desc.split("%EXP%");
  let ret = [];
  exps.forEach((exp, i) => {
    ret.push(lits[i]);
    ret.push(exp);
  });
  ret.push(lits[lits.length - 1]);
  return <p>{ret}</p>;
}

export default Runner;


