import React, { Component } from 'react';
import Editor from './MobuEditor.js';
import StatusEditor from './MobuStatus.js';
import Runner from './MobuRunner.js';
import getDistinguisher from './MobuDistinguishers.js';
import * as utils from './utils.js';
import './mobu.css';

const alpha = "ABCDEFGHIJKLMNOPQRSTUVWXYZ".split("");
const defEncounter = [
  expandCreatureGroup({
      name: "Goblin",
      number: 5,
      color: utils.tinycolor({r: 189, g: 225, b: 133}),
      distinguisher: "Humanoid traits",
      actions: [{name: "Scimitar", desc: "[1d20+4] to hit, [1d6+2] slashing damage."},
        {name: "Shortbow", desc: "[1d20+4] to hit, [1d6+2] piercing damage."}],
      HP: utils.dice.parse("2d6")
    }),
  expandCreatureGroup({
      name: "Bugbear",
      number: 1,
      color: utils.tinycolor({r: 133, g: 225, b: 176}),
      distinguisher: "Humanoid traits",
      actions: [{name: "Morningstar", desc: "[1d20+4] to hit, [2d8+2] piercing damage."},
        {name: "Javelin", desc: "[1d20+4] to hit, [2d6+2] melee or [1d6+2] ranged piercing damage."}],
      HP: utils.dice.parse("5d8+5")
    })
];

const defConditions = [
  {name: "Prone", color: utils.tinycolor("#00a4ac")},
  {name: "Incapacitated", color: utils.tinycolor("#db9f03")},
  {name: "Unconscious", color: utils.tinycolor("#031bdb")},
]

const namespace = window.location.pathname.split('/').pop();

const MODE = {
  CREATURES: 0,
  CONDITIONS: 1,
  RUN: 2
}

function makeJSONable(cg) {
  const members = cg.members.map(function(c) { return {...c, color: c.color.toHexString()}; });
  return {...cg, color: cg.color.toHexString(), HP: cg.HP.toString(), members: members};
}

function parseJSONified(cg) {
  const members = cg.members.map(function(c) {
    return {...c, color: utils.tinycolor(c.color), conditions: c.conditions || []};
  });
  return {...cg, color: utils.tinycolor(cg.color), HP: utils.dice.parse(cg.HP), members: members};
}

function makeJSONableCd(cd) {
  return {name: cd.name, color: cd.color.toHexString()};
}

function parseJSONifiedCd(cd) {
  return {name: cd.name, color: utils.tinycolor(cd.color)};
}

class Mobu extends Component {
  constructor(props) {
    super(props);
    const savedCreatures = JSON.parse(localStorage.getItem("mobu-" + namespace));
    const savedConditions = JSON.parse(localStorage.getItem("mobu-cd-" + namespace));
    this.state = {
      mode: MODE.CREATURES,
      creatures: savedCreatures ? savedCreatures.map(parseJSONified) : defEncounter,
      conditions: savedConditions ? savedConditions.map(parseJSONifiedCd) : defConditions
    };
  }

  storeCreatures(creatures) {
    localStorage.setItem("mobu-" + namespace, JSON.stringify(creatures.map(makeJSONable)));
  }

  storeConditions(conditions) {
    localStorage.setItem("mobu-cd-" + namespace, JSON.stringify(conditions.map(makeJSONableCd)));
  }

  addCreature(data) {
    const newCreatures = this.state.creatures.concat([expandCreatureGroup(data)]);
    this.setState({
      creatures: newCreatures
    }); 
    this.storeCreatures(newCreatures);
  }

  editCreature(i, data) {
    const newCreatures = utils.insertIntoNewArray(this.state.creatures, i, expandCreatureGroup(data));
    this.setState({
      creatures: newCreatures
    });
    this.storeCreatures(newCreatures);
  }

  updateCreature(i, data) {
    const newCreatures = utils.insertIntoNewArray(this.state.creatures, i, data);
    this.setState({
      creatures: newCreatures
    });
    this.storeCreatures(newCreatures);
  }

  removeCreature(i) {
    const newCreatures = this.state.creatures.slice();
    newCreatures.splice(i, 1);
    this.setState({
      creatures: newCreatures
    });
    this.storeCreatures(newCreatures);
  }

  addCondition(data) {
    const newConditions = this.state.conditions.concat([data]);
    this.setState({
      conditions: newConditions
    });
    this.storeConditions(newConditions);
  }

  removeCondition(i) {
    const newConditions = this.state.conditions.slice();
    newConditions.splice(i, 1);
    // Make sure nobody had that condition, and re-index other conditions
    const newCreatures = this.state.creatures.map(function(cg) {
      const newMembers = cg.members.map(function(c) {
        const newConditions =
          c.conditions.filter(function(j) { return j !== i; })
          .map(function(j) { if(j > i) return j - 1; return j; });
        return {...c, conditions: newConditions};
      });
      return {...cg, members: newMembers};
    });
    this.setState({
      creatures: newCreatures,
      conditions: newConditions
    });
    this.storeConditions(newConditions);
  }

  toggleMode(m) {
    this.setState({
      mode: m
    });
  }

  render() {
    return (
      <div className="mobu-container">
        <div className="nav nav-tabs mobu-tab-container" role="tablist">
          <a className="nav-item mobu-logo" href="/" aria-hidden="true"></a>
          <button className="nav-item nav-link active"
            data-toggle="pill"
            role="tab"
            type="button"
            onClick={() => this.toggleMode(MODE.CREATURES)}
            disabled={this.state.mode === MODE.CREATURES}>
            Creatures
          </button>
          <button
            className="nav-item nav-link"
            data-toggle="pill"
            role="tab"
            type="button"
            onClick={() => this.toggleMode(MODE.CONDITIONS)}
            disabled={this.state.mode === MODE.CONDITIONS}>
            Conditions
          </button>
          <button
            className="nav-item nav-link"
            data-toggle="pill"
            role="tab"
            type="button"
            onClick={() => this.toggleMode(MODE.RUN)}
            disabled={this.state.mode === MODE.RUN}>
            Run encounter
          </button>
        </div>
        {(this.state.mode === MODE.CREATURES) &&
          <Editor
            creatures={this.state.creatures}
            onAdd={(data) => this.addCreature(data)}
            onEdit={(i, data) => this.editCreature(i, data)}
            onRemove={(i) => this.removeCreature(i)}
          />}
        {(this.state.mode === MODE.CONDITIONS) &&
          <StatusEditor
            conditions={this.state.conditions}
            onAdd={(data) => this.addCondition(data)}
            onRemove={(i) => this.removeCondition(i)}
          />}
        {(this.state.mode === MODE.RUN) &&
          <Runner
            creatures={this.state.creatures}
            conditions={this.state.conditions}
            onUpdate={(i, data) => this.updateCreature(i, data)}
          />}
      </div>
    );
  }
}

function colorJitter(color, amt) {
  let a = Math.random();
  let b = Math.random();
  let hJitter = Math.min(a, b);
  let sJitter = Math.max(a, b) - hJitter;
  let lJitter = 1 - hJitter - sJitter;
  hJitter -= 0.5;
  sJitter -= 0.5;
  lJitter -= 0.5;
  let hsl = color.toHsl();
  return utils.tinycolor.fromRatio({h: (hsl.h / 360 + amt * hJitter) % 1,
                          s: Math.max(Math.min(hsl.s + amt * sJitter, 1), 0),
                          l: Math.max(Math.min(hsl.l + amt * lJitter, 1), 0)});
}

function expandCreatureGroup(cg) {
  const newCg = {...cg, members: []};

  for(let i=0;i<cg.number;i++) {
    const suffix = (cg.number === 1)
      ? ""
      : (
          (cg.distinguisher === "Number")
          ? ` ${i + 1}`
          : " " + getDistinguisher(cg.distinguisher)
        );

    let maxHP = Math.max(cg.HP.roll(), 1);
    newCg.members.push({
      name: alpha[i],
      desc: cg.name + suffix,
      color: colorJitter(cg.color, 0.1),
      maxHP: maxHP,
      HP: maxHP,
      conditions: []
    });
  }
  return newCg;
}

export default Mobu;



