function randIntIncl(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1)) + min; //The maximum is inclusive and the minimum is inclusive 
}

function getRawRoller(n, size) {
	return function() {
		let res = [];
		for (let i=0;i<n;i++)
			res.push(randIntIncl(1, size));
		//console.log('Rolled:', res);
		return res;
	};
}

var Operations = {
	'+': function(a, b) { return a + b; },
	'-': function(a, b) { return a - b; },
	'*': function(a, b) { return a * b; },
	'/': function(a, b) { return a / b; }
}

class DiceExpression {
	constructor(type, a, b, c, d) {
		switch(type) {
			case 'num':
				this.static = true;
				this.roll = this.min = this.max = function() { return a; };
				this.toString = function() { return '' + a; };
				break;
			case 'op':
				this.static = a.static && c.static;
				let op = Operations[b];
				this.roll = function() { return op(a.roll(), c.roll()); };
				this.toString = function() { return `${a.toString()} ${b} ${c.toString()}`; };
				this.min = function() { return op(a.min(), c.min()); };
				this.max = function() { return op(a.max(), c.max()); };
				break;
			case 'die':
				this.static = false;
				this.roll = function() {
					return getRawRoller(a.roll(), b.roll())().reduce(Operations['+']);
				};
				this.toString = function() { return `${a.toString()}d${b.toString()}`; };
				this.min = function() { return a.min(); };
				this.max = function() { return a.max() * b.max(); };
				break;
			case 'diex':
				// Default drop and keep to drop low and keep high.
				this.static = false;
				c = (c === 'k') ? 'kh' : c;
				c = (c === 'd') ? 'dl' : c;
				this.roll = () => {
					let rolls = getRawRoller(a.roll(), b.roll())().sort(Operations['-']);
					let d_ = (d) ? d.roll() : 1;
					d_ = Math.min(rolls.length, d_);
					switch (c) {
						case 'kh': // Keep high.
							return rolls.slice(-1 * d_).reduce(Operations['+']);
						case 'kl': // Keep low.
							return rolls.slice(0, d_).reduce(Operations['+']);
						case 'dh': // Drop high.
							return rolls.slice(0, rolls.length - d_).reduce(Operations['+']);
						case 'dl': // Drop low.
							return rolls.slice(d_ - rolls.length).reduce(Operations['+']);
					}
				};
				this.toString = function() { return `${a.toString()}d${b.toString}${c+d}`; };
				this.min = function() {
					if(c.charAt(0) === 'k') {
						return (d) ? d.min() : 1;
					}
					else {
						let d_ = (d) ? d.max() : 1;
						return Math.max(0, a.min() - d_);
					}
				};
				this.max = function() {
					if(c.charAt(0) === 'k') {
						let d_ = (d) ? d.max() : 1;
						return d_ * b.max();
					}
					else {
						let d_ = (d) ? d.min() : 1;
						return (a.max() - d_) * b.max();
					}
				};
				break;
			case 'par':
				this.static = a.static;
				this.roll = function() { return a.roll(); };
				this.toString = function() { return `(${a.toString()})`; };
				this.max = a.max;
				this.min = a.min;
		}
	}
}

function Dice() {
	return {
		parse: function(exp) {
			exp = exp.toLowerCase().replace(/\s/g, ""); // Remove all whitespace.
			let tree = {}; // Expression tree lookup.

			// Subscripts of the lookup expressions.
			let nD = 0;
			let nD_ = 0;

			// Convert all numeric literals.
			exp = exp.replace(/\d+/g, (n) => {
				let name = `%D${nD}%`;
				nD++;
				tree[name] = new DiceExpression('num', parseInt(n, 10));
				return name;
			});
			//console.log(exp);

			let last = exp;
			while(true) {
				// If we're down to only one root node, return.
				if(exp.match(/^%D_?\d+%$/)) return tree[exp];

				// Convert XdY[dk][hl]Z.
				exp = exp.replace(/(%D\d+%)d(%D\d+%)([dk][hl]?)(%D\d+%)/g, (_, a, b, c, d) => {
					let name = `%D_${nD_}%`;
					nD_++;
					tree[name] = new DiceExpression('diex', tree[a], tree[b], c, tree[d]);
					return name;
				});
				//console.log(exp);

				// Convert abbreviated XdY[dk][hl].
				exp = exp.replace(/(%D\d+%)d(%D\d+%)([dk][hl]?)/g, (_, a, b, c) => {
					let name = `%D_${nD_}%`;
					nD_++;
					tree[name] = new DiceExpression('diex', tree[a], tree[b], c);
					return name;
				});
				//console.log(exp);

				// Convert just plain XdY.
				exp = exp.replace(/(%D\d+%)d(%D\d+%)/g, (_, a, b) => {
					let name = `%D_${nD_}%`;
					nD_++;
					tree[name] = new DiceExpression('die', tree[a], tree[b]);
					return name;
				});
				//console.log(exp);

				// Convert operations.
				exp = exp.replace(/(%D_?\d+%)([-+*/])(%D_?\d+%)/g, (_, a, b, c) => {
					let name = `%D_${nD_}%`;
					nD_++;
					// Check here for divide by zero errors.
					if((b === '/') && (tree[c].min() <= 0) && (tree[c].max() >= 0))
						return "%%%DIVIDEBYZERO%%%";
					tree[name] = new DiceExpression('op', tree[a], b, tree[c]);
					return name;
				});
				//console.log(exp);

				// Convert parentheses.
				exp = exp.replace(/\((%D_?\d+%)\)/g, (_, a) => {
					let name = `%D${nD}%`;
					nD++;
					tree[name] = new DiceExpression('par', tree[a]);
					return name;
				});
				//console.log(exp);

				// If no changes were made but we have junk left over, parse failed.
				if(exp === last) return null;
				last = exp;
			}
		},

		roll: function(exp) {
			let parsed = this.parse(exp);
			return (parsed) ? parsed.roll() : null;
		}
	};
}

//exports.Dice = Dice;
export default Dice;


