import React, { Component } from 'react';
import * as utils from './utils.js';
import './mobu.css';

// const dice = Dice();
// const tinycolor = require("tinycolor2");

class Editor extends Component {
  constructor(props) {
    super(props);
    const initColor = utils.tinycolor.fromRatio({h: Math.random(), s: 0.6, l: 0.7});
    this.state = {
      isAddMode: true,
      isValidName: false,
      isValidHP: false,
      isValidActions: true,
      nextColor: initColor.spin(65),
      editCID: null,
      cName: "",
      cNumber: 1,
      cColor: initColor,
      cHP: "",
      cActions: [],
      cDistinguisher: "Number"
    };
  }

  resetState() {
    this.setState({
      isAddMode: true,
      isValidName: false,
      isValidHP: false,
      isValidActions: true,
      editCID: null,
      cName: "",
      cNumber: 1,
      cHP: "",
      cActions: []
    });
  }

  saveFirstInput(elem) {
    this.firstInput = elem;
  }

  creatureFromState() {
    // Remove empty actions.
    const actions = [];
    this.state.cActions.forEach(function(a) {
      if(a.name.length > 0) actions.push(a);
    })
    return {
      name: this.state.cName,
      number: this.state.cNumber,
      color: this.state.cColor.clone(),
      HP: utils.dice.parse(this.state.cHP),
      actions: actions,
      distinguisher: this.state.cDistinguisher
    };
  }

  handleInputFocus(e) {
    e.target.setSelectionRange(0, 9999);
  }

  handleNameChange(e) {
    const name = e.target.value;
    this.setState({
      isValidName: (name.length > 0) && 
        !(this.props.creatures.some((c, i) => {
          return (c.name === name) && (i !== this.state.editCID); })),
      cName: name
    });
  }

  handleNumberChange(e) {
    const num = Math.min(Math.max(parseInt(e.target.value, 10) || 1, 1), 20);
    this.setState({cNumber: num});
  }

  handleHPChange(e) {
    const HPexp = utils.dice.parse(e.target.value);
    this.setState({
      isValidHP: (HPexp != null),
      cHP: e.target.value
    });
  }

  handleDistinguisherChange(e) {
    this.setState({
      cDistinguisher: e.target.value
    });
  }

  handleActionNameChange(e, i) {
    const actions = this.state.cActions;
    const newName = e.target.value.replace(" ", "_");
    this.setState({
      isValidActions:
        !this.state.cActions.some((a) => { return a.name === newName; })
        && !(newName.match(/^damage$/i)) && !(newName.match(/^heal$/i)) && !(newName.match(/^condition$/i)),
      cActions: utils.insertIntoNewArray(actions, i, {name: newName, desc: actions[i].desc})
    });
  }

  handleActionDescChange(e, i) {
    const actions = this.state.cActions;
    this.setState({
      cActions: utils.insertIntoNewArray(actions, i, {name: actions[i].name, desc: e.target.value})
    });
  }

  handleAddAction() {
    const actions = this.state.cActions;
    this.setState({
      cActions: actions.concat([{name: "", desc: ""}])
    });
  }

  handleRemoveAction(i) {
    const actions = this.state.cActions.slice();
    actions.splice(i, 1);
    this.setState({
      cActions: actions
    })
  }

  handleAddCreature(e) {
    const creature = this.creatureFromState();
    this.setState({
      nextColor: this.state.nextColor.spin(65),
      cColor: this.state.nextColor
    })
    this.props.onAdd(creature);
    this.resetState();
  }

  handleStartEditCreature(i) {
    const creature = this.props.creatures[i];
    this.setState({
      isAddMode: false,
      isValidName: true,
      isValidHP: true,
      editCID: i,
      cName: creature.name,
      cNumber: creature.number,
      cColor: creature.color,
      cDistinguisher: creature.distinguisher,
      cHP: creature.HP.toString(),
      cActions: creature.actions
    });
  }

  handleDoneEditCreature() {
    const creature = this.creatureFromState();
    this.props.onEdit(this.state.editCID, creature);
    this.resetState();
  }

  handleRemoveCreature(i) {
    this.props.onRemove(i);
  }

  handleSubmit(e) {
    e.preventDefault();
    (this.state.isAddMode) ? this.handleAddCreature(e) : this.handleDoneEditCreature();
    this.firstInput.focus();
  }

  render() {
    const creatureCards = this.props.creatures.map((c, i) => {
      return (
        <CreatureEditListCard
          key={i}
          c={c}
          onEditBtn={() => this.handleStartEditCreature(i)}
          onRemoveBtn={() => this.handleRemoveCreature(i)}
        />
      );
    });

    const actionEdits = this.state.cActions.map((a, i) => {
      return (
        <ActionEditRow
          key={i}
          actionName={a.name}
          actionDesc={a.desc}
          onNameChange={(e) => this.handleActionNameChange(e, i)}
          onDescChange={(e) => this.handleActionDescChange(e, i)}
          onRemoveBtn={() => this.handleRemoveAction(i)}
        />
      );
    })

    return (
      <div className="mobu-component-container">
        <div className="mobu-edit-list">
          {creatureCards}
        </div>
        <div className="mobu-edit-form">
          <form onSubmit={(e) => this.handleSubmit(e)}>
            <div className="form-row">
              <div className="form-group col">
                <label htmlFor="cName">Name</label>
                <input ref={(elem) => this.saveFirstInput(elem)}
                  className="form-control"
                  id="cName"
                  type="text"
                  value={this.state.cName}
                  onChange={(e) => this.handleNameChange(e)}
                />
              </div>
              <div className="form-group col">
                <label htmlFor="cNum">Number</label>
                <input
                  className="form-control"
                  id="cNum"
                  type="tel"
                  value={this.state.cNumber}
                  onChange={(e) => this.handleNumberChange(e)}
                  onFocus={this.handleInputFocus}
                />
              </div>
              <div className="form-group col">
                <label htmlFor="cHP">HP</label>
                <input
                  className="form-control"
                  id="cHP"
                  type="text"
                  value={this.state.cHP}
                  onChange={(e) => this.handleHPChange(e)}
                />
              </div>
            </div>
            {(this.state.cNumber > 1) &&
              <DistinguisherSelect
                value={this.state.cDistinguisher}
                onChange={(e) => this.handleDistinguisherChange(e)}
              />
            }
            <label htmlFor="cActions" style={{marginRight: "0.5em"}}>Actions:</label> 
            <button className="btn btn-secondary" type="button" onClick={() => this.handleAddAction()}>+</button>
            <div id="cActions" className="form-group mobu-edit-subunit">
              {actionEdits}
            </div>
            <input
              className="btn btn-secondary"
              type="submit"
              value={this.state.isAddMode ? "Add creature" : "Done editing"}
              disabled={!(this.state.isValidName && this.state.isValidHP && this.state.isValidActions)}
            />
          </form>
        </div>
      </div>
    );
  }
}

class DistinguisherSelect extends Component {
  render() {
    return (
      <div className="form-group mobu-edit-subunit">
        <label htmlFor="cDistinguisher">Distinguished by:</label>
        <select className="form-control" id="cDistinguisher" value={this.props.value} onChange={(e) => this.props.onChange(e)}>
          <option value="Number">Number</option>
          <option value="Humanoid traits">Humanoid traits</option>
          <option value="Mammalian traits">Mammalian traits</option>
          <option value="Reptilian traits">Reptilian traits</option>
          <option value="Insectoid traits">Insectoid traits</option>
          <option value="Fish traits">Fish traits</option>
          <option value="Bird traits">Bird traits</option>
          <option value="Dragon traits">Dragon traits</option>
          <option value="Undead traits">Undead traits</option>
          <option value="Lovecraftian traits">Lovecraftian traits</option>
          <option value="Amorphous traits">Amorphous traits</option>
        </select>
      </div>
    );
  }
}

function CreatureEditListCard(props) {
  const actionList = props.c.actions.map((a, i) => {
    return (
      <li key={i} className="list-inline-item badge badge-pill badge-secondary">{a.name}</li>
    );
  })
  return (
    <div className="mobu-edit-block" style={{backgroundColor: props.c.color}}>
      <h4 className="mobu-card-title">{props.c.name}{(props.c.number > 1) ? ` (${props.c.number})` : ""}</h4>
      <div className="mobu-edit-block-buttons">
        <button className="btn btn-secondary" type="button" onClick={props.onEditBtn}>Edit</button>
        <button className="btn btn-secondary" type="button" onClick={props.onRemoveBtn}>&times;</button>
      </div>
      <CreatureDescriptor name="HP" value={props.c.HP.toString()}/>
      {(props.c.number > 1) &&
        <CreatureDescriptor name="Distinguished by" value={props.c.distinguisher}/>
      }
      {(props.c.actions.length > 0) &&
        <CreatureDescriptor name="Actions" value=""/>}
      <ul className="list-inline">
        {actionList}
      </ul>
    </div>
  );
}

function CreatureDescriptor(props) {
  return (
    <div className="descriptor">
      <strong>{props.name}:</strong> {props.value}
    </div>
  );
}

function ActionEditRow(props) {
  return (
    <div className="form-row mobu-edit-subunit">
      <input
        className="form-control col-3"
        type="text"
        value={props.actionName}
        onChange={props.onNameChange}
        placeholder="Name"
      />
      <input
        className="form-control col-8"
        type="text"
        value={props.actionDesc}
        onChange={props.onDescChange}
        placeholder="Description"
      />
      <button className="btn btn-secondary" type="button" onClick={props.onRemoveBtn}>&times;</button>
    </div>
  );
}

export default Editor;
export { CreatureDescriptor };


