import React, { Component } from 'react';
import { CustomPicker } from 'react-color';

var { Hue, Saturation } = require('react-color/lib/components/common');

class FlexibleColorPicker extends Component {

  handleChange(e) {
    this.props.onChange(e);
  }

  render() {
    return (
      <div className="mobu-color-container">
        <div className="mobu-color-saturation">
          <Saturation {...this.props} pointer={ FlexibleCursor } onChange={(e) => this.handleChange(e)}/>
        </div>
        <div className="mobu-color-hue">
          <Hue {...this.props} pointer={ HorizontalCursor } onChange={ (e) => this.handleChange(e) } direction="vertical" />
        </div>
      </div>
    );
  }
}

function FlexibleCursor(props) {
  const cursorStyles = {
    borderRadius: "50%",
    height: "0.6em",
    width: "0.6em",
    boxShadow: "rgb(255, 255, 255) 0px 0px 0px 0.15em, rgba(0, 0, 0, 0.3) 0px 0px 0.1em 0.1em inset, rgba(0, 0, 0, 0.4) 0px 0px 0.1em 0.2em",
    transform: "translate(-0.3em, -0.3em)"
  }
  return (
    <div style={cursorStyles}></div>
  );
}

function HorizontalCursor(props) {
  const cursorStyles = {
    left: "0.1em",
    height: "0.3em",
    width: "2em",
    boxShadow: "rgb(255, 255, 255) 0px 0px 0px 0.15em, rgba(0, 0, 0, 0.3) 0px 0px 0.1em 0.1em inset, rgba(0, 0, 0, 0.4) 0px 0px 0.1em 0.2em"
  }
  return (
    <div style={cursorStyles}></div>
  );
}

export default CustomPicker(FlexibleColorPicker);


