import React, { Component } from 'react';
import MobuColorPicker from './MobuColorPicker.js';
import * as utils from './utils.js';
import './mobu.css';

class StatusEditor extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isValidName: false,
      isShowColorPicker: false,
      name: "",
      color: utils.tinycolor.fromRatio({h: Math.random(), s: 0.8, l: 0.3})
    };
  }

  saveFirstInput(elem) {
    this.firstInput = elem;
  }

  handleShowColorPicker(e) {
    this.setState({ isShowColorPicker: !this.state.isShowColorPicker });
  }

  handleCloseColorPicker() {
    this.setState({ isShowColorPicker: false });
  }

  handleSubmit(e) {
    e.preventDefault();
    this.props.onAdd({name: this.state.name, color: this.state.color});
    this.setState({
      isValidName: false,
      name: "",
      color: this.state.color.clone().spin(65)
    });
  }

  handleNameChange(e) {
    const name = e.target.value;
    this.setState({
      isValidName: (name.length > 0) && 
        !(this.props.conditions.some((c, i) => { return (c.name === name); })),
      name: name
    });
  }

  handleColorChange(c) {
    const newColor = utils.tinycolor(c.rgb);
    this.setState({
      color: newColor
    });
  }

  handleRemoveCondition(i) {
    this.props.onRemove(i);
  }

  render() {
    const statusCards = this.props.conditions.map((c, i) => {
      return <StatusCard key={i} name={c.name} color={c.color} onRemoveBtn={() => this.handleRemoveCondition(i)}/>
    });
    return (
      <div className="mobu-component-container">
        <div className="mobu-edit-list">
          {statusCards}
        </div>
        <div className="mobu-edit-form">
          <form onSubmit={(e) => this.handleSubmit(e)}>
            <div className="form-row">
              <div className="form-group col">
                <label htmlFor="sName">Name</label>
                <input ref={(elem) => this.saveFirstInput(elem)}
                  className="form-control"
                  id="sName"
                  type="text"
                  value={this.state.name}
                  onChange={(e) => this.handleNameChange(e)}
                />
              </div>
              <div className="form-group col">
                <label htmlFor="sColor">Color</label>
                <div className="mobu-color-swatch" id="sColor">
                  <div className="mobu-color-color" style={{backgroundColor: this.state.color}} onClick={(e) => this.handleShowColorPicker(e)}></div>
                </div>
                { this.state.isShowColorPicker ?
                  <div className="mobu-color-popover">
                    <div className="cover" onClick={() => this.handleCloseColorPicker()}></div>
                    <MobuColorPicker color={this.state.color.toHexString()} onChange={(c) => this.handleColorChange(c)}/>
                  </div> : null}
              </div>
            </div>
            <input
              className="btn btn-secondary" 
              type="submit"
              value="Add status condition"
              disabled={!(this.state.isValidName)}
            />
          </form>
        </div>
      </div>
    );
  }
}

function StatusCard(props) {
  return (
    <div className="mobu-status-block" style={{backgroundColor: props.color}}>
      <h2 className="mobu-card-title light">{props.name}</h2>
      <div className="mobu-edit-block-buttons">
        <button className="btn btn-secondary" type="button" onClick={props.onRemoveBtn}>&times;</button>
      </div>
    </div>
  );
}

export default StatusEditor;


